body {}

.container {
  max-width: 79.688vw; // 1248px //79.792vw
  @include responsive(1600px, 0) {
    max-width: 90vw; }
  @include mobile {
    max-width: 100%; } }
.row {
  margin-left: -12px;
  margin-right: -12px; }

[class*='col-'] {
  padding-left: 12px;
  padding-right: 12px; }

ul {
  list-style: none;
  margin-bottom: 0; }

main {}

section {
  .section-title {
    h2 {
      text-align: center;
      position: relative;
      &:after {
        content: "";
        width: rem(90);
        height: 1px;
        background: #000000;
        position: absolute;
        top: calc(100% + 19px);
        left: 0;
        right: 0;
        margin: 0 auto; } } }
  @include maxtablet {
    &:not(.home-masthead):not(.masthead-small) {
