.btn {
  padding: rem(12) rem(38);
  border-radius: 50px;
  display: inline-block;
  text-decoration: none;
  font-size: 16px;
  line-height: 24px;
  transition: all .3s ease;
  &:hover {
    text-decoration: none;
    color: $text; }
  &-primary {
    background-color: #0083CB;
    color: #fff;
    &:hover {
      color: #fff;
      background-color: darken(#0083CB, 10%); } }
  &-link {
    color: #3276B2;
    padding: 0;
    &:hover {
      color: #3276B2;
      text-decoration: underline; } }
  &-transparent {
    padding: 12px 34px;
    border: 2px solid #fff;
    border-radius: 8px;
    text-align: center;
    color: #fff;
    &:hover {
      color: $dark;
      background: #fff; } }
  &-large {
    padding: rem(20) rem(50); }
  &-long {
    width: rem(368);
    text-align: center; }

  @include maxtablet {
    &-long {
      width: 100%; } } }

