.masthead {
  &-small {
    position: relative;
    min-height: rem(515);
    .masthead-bg {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
      object-position: center;
      background-attachment: fixed;
      background-position: top;
      background-repeat: no-repeat;
      background-size: auto; }
    .masthead-text {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      color: #FFF;
      &:after {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, .3); }
      h1 {
        color: #FFF;
        margin-bottom: rem(0);
        font-size: rem(50);
        line-height: rem(60);
        @include font-600; }
      .container {
        height: 100%;
        align-items: flex-end;
        padding-bottom: rem(80);
        display: flex;
        position: relative;
        z-index: 4;
        .row {
          width: 100%;
          align-items: flex-end; } } }
    .masthead-date {
       font-size: rem(20);
       line-height: rem(24);
       text-align: right;
       display: block; }
    @include mobile {
      .masthead-text {
        h1 {
          font-size: 36px;
          line-height: 1.3em;
          br {
            display: none; } } } } } }

.banner {
