        line-height: 32px;
        color: #616161; } } } }


.icon__card {
  width: 100%;
  height: 100%;
  padding: 32px 20px;
  border-radius: 8px;
  border: 1px solid #EEEEEE;
  background: #fff;
  box-shadow: 0 4px 4px 0 rgba(0,0,0,0.2);
  transition: all .4s linear;
  display: flex;
  flex-direction: column;
  position: relative;
  .card-link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
  .icon {
    width: 68px;
    height: 68px;
    border-radius: 50%;
    margin-bottom: 32px;
    overflow: hidden;
    background: #77A1CA;
    display: flex;
    &.mobile {
      display: none; }
    img,
    .svg {
      width: auto;
      max-height: 36px;
      height: 100%;
      display: block;
      object-fit: contain;
      margin: auto;
      filter: brightness(100);
      path {
        fill: red; } } }
  h4 {
    font-size: 20px;
    line-height: 24px;
    font-family: "Rubik Medium";
    font-weight: 500;
    margin-bottom: 12px;
    transition: all .4s linear; }
  .text {
    margin-bottom: 32px;
    p {
      min-height: 100px; } }
  .btn-link {
    display: inline-block;
    margin-top: auto;
    padding: 0;
    text-decoration: none;
    .ic {
      display: inline-block;
      vertical-align: middle;
      width: 16px;
      height: 16px;
      img,
      .svg {
        display: block;
        width: 16px;
        height: 16px;
        max-width: 100%;
        object-fit: contain; } } }
  &:hover {
    background: #3276B2;
    border-color: #3276B2;
    box-shadow: 0 4px 4px 0 rgba(0,0,0,0.2);
    color: #fff;
    cursor: pointer;
    .icon {
      background: #fff;
      img,
      .svg {
        filter: none; } }
    .btn-link,
    h4 {
      color: #fff; }
    .svg {
      path {
        fill: #fff; } } } }


